/* Container da checkbox */
.container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Troca o cursor do mouse quando passa na checkbox */
.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Customização da checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 18px;
  width: 18px;
  margin-top: 0.4%;
  margin-bottom: 0%;
  border-width: 0.5pt;
  border-style: solid;
  border-radius: 0.1cm;
  margin-left: 1.5%;
  background-color: #eee;
}

/* Fica cinza quando passa o mouse em cima */
.container:hover input ~ .checkmark {
  background-color: #ccc;
}

/* Quando aparecer o checkmark, troca a cor do fundo */
.container input:checked ~ .checkmark {
  background-color: #ed7d31;
}

/* Cria o ícone do checkmark, que é transparente antes de dar check */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Mostra o checkmark quando clica */
.container input:checked ~ .checkmark:after {
  display: block;
}

/* Customização do checkmark */
.container .checkmark:after {
  left: 4.5px;
  width: 7px;
  height: 13px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

