}

/* Butão entrar em contato */
.button-entraremcontato{
  text-align: left;
  width: 4.50cm;
  height: 0.8cm;
  border-width: 0.5pt;
  border-style: solid;
  border-color: rgb(237,125,49);
  font-size: 12pt;
  background: rgb(237,125,49);
  font-family: GlacialIndifference;
  color: rgb(255,255,255);
  padding-left: 2%;
