  transition: 0.3s;
}

/* Hover botao do swal */
.button-entraremcontato:hover {
  background-color: rgb(255, 255, 255) !important;
  color: rgb(237, 125, 49);
  transition: 0.3s;
}


/* Row com profissoes prestador*/
.row-professionperfil {
  display: block;
  margin-top: 5%;
  margin-left: 0px;
}

/* Texto a dentro da Row com profissoes prestador*/
.row-professionperfil p {
  font-family: GlacialIndifference;
  font-size: 12pt;
  text-align: left;
  color: rgb(64, 64, 64) !important;
  margin-bottom: 2%;
  margin-left: 2%;
}

/* Deixa maiústula a primeira letra de cada palavra no caminho */
.row-professionperfil p::first-letter {
  text-transform: capitalize;
}

/* Estilo da caixa de descrição */
.perfil-description {
  border: solid 1px rgba(191, 191, 191);
