}

/* Desce a barra suavemente quando clica numa letra do dicionário*/
html {
	scroll-behavior: smooth;
  scroll-padding:  140px;
}

/* Aumenta a suavidade que a barra desce quando clica na letra do dicionário*/
@media screen and (prefers-reduced-motion: reduce) {
	html {
		scroll-behavior: auto;
	}
}

/* Define a cor do serviço para cinza antes de passar o mouse */
.job-link a {
  text-decoration: none;
  color: rgb(118,113,113);
