}

/* Lugar onde fica o ícone cinza */
.job-icon-gray-class {
  width: 65px;
	height: 65px;
	border-style: none;
  margin-right: auto;
  margin-top: 1px;
  margin-bottom: 1px;
  display: inline;
  position: absolute;
}

/* Lugar onde fica o ícone laranja transparente */
.job-icon-orange-class {
	width: 65px;
	height: 65px;
  border-style: none;
  margin-right: auto;
  margin-top: 1px;
  margin-bottom: 1px;
  display: inline;
  position: relative;
  z-index: 2;
  opacity: 0;
  transition: 0.3s;
}

/* Deixa o ícone laranja opaco */
.job-link:hover .job-icon-orange-class {
  opacity: 100;
  transition: 0.3s;
}
